import onfire from '../../static/js/onfire.js';
export default {
	data() {
		return {
			img:[],
			list:[],
			pagemax:'',//总的数量
			pagenow:0,//当前的页数
			shownumber:12,//当前页面的条数
			pagenumber:12,//每页的分页条数
			keywords:'',
			
			contentText: {
				contentdown: "查看更多",
				contentrefresh: "加载中",
				contentnomore: "没有更多"
			},
			status:'loading',
			shouLoading:true,
			haveData:false,
			
			selectPeople:[],
			key:'',
			content:'',
			
			selectAll:false,
			selectNum:0
		}
	},
	onLoad(e) {
		var t=this;	
		t.content=e.people_list;
		t.img[0]=t.sta.sitestatic+'img/dl-icon.png';
		//获取新闻列表
		t.getDangList();
	},
	onUnload() {
		this.max = 0,
		this.data = [],
		this.loadMoreText = "加载更多",
		this.showLoadMore = false;
	},
	onReachBottom() {
		if (this.shownumber >= this.pagemax) {
			return;
		}
		setTimeout(() => {
			this.setDate();
		}, 100);
	},
	onPullDownRefresh() {
		this.initData();
	},	
	methods: {
		selectAllPeople:function(){
			var t=this;
			t.content=[];
			t.selectPeople=[];
			if(t.selectAll==false){
				t.selectAll=true;
				t.selectNum=t.list.length;
				for(var i=0;i<t.list.length;i++){
					t.list[i]['checked']=true;
					t.selectPeople.push(t.list[i]);
				}
			}else{
				t.selectAll=false;
				t.selectNum=0;
				for(var i=0;i<t.list.length;i++){
					t.list[i]['checked']=false;
				}
				t.selectPeople=[];
			}
		},
		peopleCheck:function(index){
			var t=this;
			var name_list='';
			var no_list='';
			for(var i=0;i<t.selectPeople.length;i++){
				if(name_list==''){
					name_list=t.selectPeople[i].communist_name
				}else{
					name_list=name_list+','+t.selectPeople[i].communist_name;
				}
				if(no_list==''){
					no_list=t.selectPeople[i].communist_no
				}else{
					no_list=no_list+','+t.selectPeople[i].communist_no;
				}
			}
			var arr=[];
			arr.push(no_list)
			arr.push(name_list)
			arr.push(t.selectPeople.length)
			onfire.fire('people_list', arr);
			history.go(-1);
		},
		checkboxChange: function (communist_no) {
			var t=this;
			var is_have=false;
			for(var i=0;i<t.list.length;i++){
				if(communist_no==t.list[i]['communist_no']){
					t.list[i]['checked']=t.list[i]['checked']==true?false:true;
					
					if(!t.selectPeople){
						t.selectPeople=t.list[i];
						t.selectNum++;
					}else{
						for(var j=0;j<t.selectPeople.length;j++){
							if(communist_no==t.selectPeople[j]['communist_no']){
								t.selectPeople.splice(j,1);
								t.selectNum--;
								is_have=true;
							}
						}
					}
				}
			}
			
			if(is_have==false){
				for(var i=0;i<t.list.length;i++){
					if(communist_no==t.list[i]['communist_no']){
						t.selectPeople.push(t.list[i]);
						t.selectNum++;
					}
				}
			}
		},
		search:function(){
			uni.showLoading();
			var t=this;
			t.selectAll=false;
			var data={
				party_no:t.globalData.userInfo.party_no,
				keywords:t.keywords,
				pagesize:999,
				page:0,
			}
			t.urlRequest.urlRequest(t.func.getDangList,data,function(res){
				var data=t.dealData(res);
				t.list=data;
				uni.hideLoading();
			})
		},
		getDangList:function(){
			uni.showLoading();
			var t=this;
			var data={
				party_no:t.globalData.userInfo.party_no,
				pagesize:t.shownumber,
				page:t.pagenow,
			}
			t.urlRequest.urlRequest(t.func.getDangList,data,function(res){
				var data=t.dealData(res);
				t.list=data;
				for (var i = 0; i < t.list.length; i++) {
					if(t.content){
						var arr=t.content.split(',');
						for(var j=0;j<arr.length;j++){
							if(t.list[i]['communist_no']==arr[j]){
								t.list[i]['checked']=true;
								t.selectPeople.push(t.list[i])
								t.selectNum++;
							}
						}
					}
				}
				
				uni.hideLoading();
			})
			
		},
		initData(){
			uni.showLoading();
			const t = this;
			t.shownumber=12;
			t.pagenow=0;
			var data={
				party_no:t.globalData.userInfo.party_no,
				pagesize:t.shownumber,
				page:t.pagenow,
			}
			t.urlRequest.urlRequest(t.func.getDangList,data,function(res){
				var data=t.dealData(res);
				t.list=data;
				for (var i = 0; i < t.list.length; i++) {
					if(t.content){
						var arr=t.content.split(',');
						for(var j=0;j<arr.length;j++){
							if(t.list[i]['communist_no']==arr[j]){
								t.list[i]['checked']=true;
								t.selectPeople.push(t.list[i])
							}
						}
					}
				}
				uni.hideLoading();
				uni.stopPullDownRefresh();
			})
		},
		setDate() {
			uni.showLoading();
			this.shownumber=this.pagenow*this.pagenumber;
			this.pagenow++;
			var t=this;
			
			var data={
				party_no:t.globalData.userInfo.party_no,
				page:t.pagenow,
				pagesize:t.pagenumber
			}
			t.urlRequest.urlRequest(t.func.getDangList,data,function(res){
				if(res.data.list){
					setTimeout(() => {
						t.haveData =true
					}, 100);
					var data=t.dealData(res);
					t.selectAll=false;
					t.list = t.list.concat(data);
					if(t.content){
						t.selectNum=0;
						t.selectPeople=[];
						for (var i = 0; i < t.list.length; i++) {
							t.list[i]['checked']=false;
							var arr=t.content.split(',');
							for(var j=0;j<arr.length;j++){
								if(t.list[i]['communist_no']==arr[j]){
									t.list[i]['checked']=true;
									t.selectNum++;
									t.selectPeople.push(t.list[i])
								}
							}
						}
					}
				}
				
				uni.hideLoading();
			})
			
		},
		dealData:function(res){
			var t=this;
			t.pagemax=res.data.count
			if(res.data.list){
				for (var i = 0; i < res.data.list.length; i++) {
					res.data.list[i]['checked']=false;
					if(res.data.list[i]['communist_avatar']){
						res.data.list[i]['img']=t.sta.siteupload+res.data.list[i]['communist_avatar'];
					}else{
						res.data.list[i]['img']=t.sta.sitestatic+'img/oa_img_head.png';
					}
					if(t.content){
						var arr=t.content.split(',');
						for(var j=0;j<arr.length;j++){
							if(res.data.list[i]['communist_no']==arr[j]){
								res.data.list[i]['checked']=true;
							}
						}
					}
				}
				
			}
			return res.data.list
		}
	}
}
